import {
  ISetParagraphBgColor,
  ISetParagraphColor,
  ISelectRange,
} from './types';

export const PARAGRAPH_SET_COLOR = 'PARAGRAPH_SET_COLOR';
export const PARAGRAPH_SET_BG_COLOR = 'PARAGRAPH_SET_BG_COLOR';
export const PARAGRAPH_SELECT_RANGE = 'PARAGRAPH_SELECT_RANGE';


export function setParagraphColor(payload: ISetParagraphColor) {
  return {
    type: PARAGRAPH_SET_COLOR,
    payload,
  };
}

export function setParagraphBgColor(payload: ISetParagraphBgColor) {
  return {
    type: PARAGRAPH_SET_BG_COLOR,
    payload,
  };
}

export function selectRange(payload: ISelectRange) {
  return {
    type: PARAGRAPH_SELECT_RANGE,
    payload,
  };
}
